import uniSearchBar from '../uni-ui/uni-search-bar/uni-search-bar.vue'
import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	name: "",
	components: {
		uniSearchBar
	},
	props: {},
	data() {
		return {
			indexVal: ''
		}
	},
	methods: {
		...mapMutations(['setLocation']),
		goToSearch() {
			uni.navigateTo({
				url: '../../pages/search/search',
				success: res => {},
				fail: () => {},
				complete: () => {

					uni.hideKeyboard();




				}
			});
		},
		// 打开地图
		openMap() {
			let t = this
			uni.chooseLocation({
				success(res) {
					t.setLocation(res.address)
				}
			})
		},
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {
		...mapState(['locations']),
	},
	watch: {

	},
	directives: {

	}
}
